import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Slices from "../components/slices"
import PageCategoryNav from "../components/pageCategoryNav"

import Title from "../components/layout/title"
import TwoCols from "../components/layout/twoCols"
import BreadCrumbs from "../components/layout/breadCrumbs"

export default function PageTemplate({ pageContext }) {
  const page = pageContext.page
  const pageCategory = pageContext.pageCategory

  return (
    <Layout>
      <SEO title={`${page.Title}`} />

      <div>
        {pageCategory && (
          <BreadCrumbs>
            {[<Link to={`/${pageCategory.UID}`}>‚Üê {pageCategory.Name}</Link>]}
          </BreadCrumbs>
        )}

        <Title>{page.Title}</Title>

        <TwoCols>
          <div>
            <Slices content={page.Content} />
          </div>
          {pageCategory && (
            <PageCategoryNav category={pageCategory} currentUID={page.UID} />
          )}
        </TwoCols>
      </div>
    </Layout>
  )
}
