import React from "react"
import { Link } from "gatsby"

export default function PageCategoryNav({ category, currentUID }) {
  return (
    <aside>
      <h3>Also in this category</h3>

      <ul>
        {category.pages.map(page => (
          <li key={page.UID}>
            {page.UID === currentUID ? (
              <strong>
                <Link to={`/${category.UID}/${page.UID}`}>{page.Title}</Link>
              </strong>
            ) : (
              <Link to={`/${category.UID}/${page.UID}`}>{page.Title}</Link>
            )}
          </li>
        ))}
      </ul>
    </aside>
  )
}
